body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.no-error{
  color: red;
  font-size: 16px;
  display: none;
}
.error{
  color: red;
  font-size: 16px;
  display: block;
}

span {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }

td {
    text-align: center; }

.before {
    float: left;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }

.month-year {
    float: left;
    margin-left: 50px;
    margin-right: 50px; }

.after {
    float: left; }

.month-year-parent {
    display: flex;
    justify-content: center; }
.calendar-container{
    display: none;
}
.calendar-container-visivle{
    display: block;
}

span {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }

td {
    text-align: center; }

.before {
    float: left;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }

.month-year {
    float: left;
    margin-left: 50px;
    margin-right: 50px; }

.after {
    float: left; }

.month-year-parent {
    display: flex;
    justify-content: center; }
.calendar-container{
    display: none;
}
.calendar-container-visivle{
    display: block;
}
table{
    margin: 0 auto;
}
